import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import {
  StyledMenu,
  FlexSpaceBetween,
  StyledFilterPanelHeading,
  StyledLinkButton
} from "./styles";
const { SubMenu } = StyledMenu;

class FilterPanel extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {};

    this.clearFilters = this.clearFilters.bind(this);
  }

  clearFilters(ev) {}

  render() {
    return (
      <StyledMenu
        onClick={this.handleClick}
        style={{ width: 256 }}
        mode="inline"
      >
        <StyledMenu.Item>
          <FlexSpaceBetween>
            <StyledFilterPanelHeading>Filters</StyledFilterPanelHeading>
            <StyledLinkButton>Clear All</StyledLinkButton>
          </FlexSpaceBetween>
        </StyledMenu.Item>
        {this.props.filters.map((filter, i) => {
          return (
            <SubMenu
              key={`sub${i}`}
              title={
                <FlexSpaceBetween>
                  <span>{filter.title}</span>
                  <span onClick={this.clearFilters}>x</span>
                </FlexSpaceBetween>
              }
            >
              <StyledMenu.ItemGroup key={`g${i}`}>
                {filter.renderOptions()}
              </StyledMenu.ItemGroup>
            </SubMenu>
          );
        })}
      </StyledMenu>
    );
  }
}

FilterPanel.propTypes = {
  filters: PropTypes.array
};

FilterPanel.defaultProps = {
  filters: []
};

export default FilterPanel;
