import styled, { css, keyframes } from 'styled-components';
import {
  maxWidth,
  minWidth,
  width,
  fontSize,
  color,
  height,
  boxShadow,
  borderRadius,
  borderColor,
  space,
} from 'styled-system';
import { WHITE, BLUE500, ACCENT_DARK, BLUE800, GREY100, GREY300, GREY400, GREY500 } from '../../theme';

const StyledPlainButton = styled.button`
  appearance: none;
  background-color: transparent;
  font-size: 14px;
  box-shadow: none;
  text-decoration: none;
  border: none;
  outline: none;
  padding: 0;
  margin: 0;
  transition: background-color 250ms ease-out, color 250ms ease-out, border-color 250ms ease-out;
  cursor: pointer;
  ${maxWidth};
  ${width};
  ${space};
  ${height};
  ${color};
  ${fontSize};
  ${borderRadius};
  ${boxShadow};
  ${borderColor};

  &[disabled] {
    opacity: 0.5;
    cursor: not-allowed;
  }
`;

const StylesDefaultButton = css`
  color: ${BLUE500};
  border-color: ${BLUE500};

  &:hover,
  &:focus {
    color: ${ACCENT_DARK};
    border-color: ${ACCENT_DARK};
  }

  &:active {
    color: ${BLUE800};
    border-color: ${BLUE800};
  }

  &[disabled] {
    color: ${BLUE500};
    border-color: ${BLUE500};
    cursor: not-allowed;
  }
`;

const StylesPrimaryButton = css`
  color: ${WHITE};
  border-color: ${BLUE500};
  background-color: ${BLUE500};

  &:hover,
  &:focus {
    background-color: ${ACCENT_DARK};
    border-color: ${ACCENT_DARK};
  }

  &:active {
    background-color: ${BLUE800};
    border-color: ${BLUE800};
  }

  &[disabled] {
    color: ${WHITE};
    border-color: ${BLUE500};
    background-color: ${BLUE500};
    cursor: not-allowed;
  }
`;

const StylesSecondaryButton = css`
  color: ${GREY500};
  border-color: ${GREY300};
  background-color: ${GREY100};

  &:hover,
  &:focus {
    color: ${ACCENT_DARK};
  }

  &:active {
    color: ${BLUE800};
    border-color: ${GREY400};
  }

  &[disabled] {
    color: ${GREY500};
    border-color: ${GREY300};
    background-color: ${GREY100};
    cursor: not-allowed;
  }
`;

const StylesCardButton = css`
  color: ${BLUE500};
  background-color: ${WHITE};
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  border-color: ${GREY100};
`;

const styles = {
  default: StylesDefaultButton,
  primary: StylesPrimaryButton,
  secondary: StylesSecondaryButton,
  card: StylesCardButton,
};

const sizes = {
  large: 48,
  default: 40,
  small: 32,
  smaller: 24,
};

const StyledButton = styled(StyledPlainButton)`
  font-weight: 600;
  text-transform: ${({ textTransform }) => textTransform};
  border: 1px solid;
  padding: 0 10px;
  border-radius: 4px;
  display: inline-flex;
  justify-content: center;
  flex-flow: row nowrap;
  white-space: nowrap;
  align-items: center;
  font-size: 12px;
  height: ${({ size }) => sizes[size]}px;
  min-width: ${({ size }) => sizes[size] * 3}px;
  ${({ colored }) => styles[colored]};
  ${minWidth};
  ${space};
`;

const rotate360 = keyframes`
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
`;

const StyledSpinner = styled.div`
  width: 22px;
  height: 22px;
  border-radius: 100%;
  border: 2px solid currentColor;
  border-bottom-color: transparent;
  animation: ${rotate360} 1.4s infinite linear;
`;

export { StyledPlainButton, StyledButton, StyledSpinner };
